import React from "react";
import "./Header.css";
function Headerim() {
  return (
    <div className="header">
      <div >
       <img className="header-image"src="/images/img-home.jpg" alt="" />
      </div>
      <div className="title">
        <h1 className="adventure">ADVENTURE AWAITS</h1>
        <p className="waiting"> What are you waiting for?</p>
        <div className="buttonContainer">
          <button className="headerButton">GET STARTED</button>
          <button className="headerButton" id='trailor'>
            WATCH TRAILOR  <i  id='play' class="fas fa-play"></i>
          </button>
        </div>
      </div>
    </div>
  );
}

export default Headerim;
